import React, {Component} from 'react';
import {HashRouter, BrowserRouter, Route, withRouter, Switch, Redirect} from "react-router-dom";
import './App.css';
import Navbar from "./components/Navbar/Navbar";
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Settings from "./components/Settings/Settings";
import Login from "./components/Login/Login";
import UsersContainer from "./components/Users/UsersContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./redux/app-reducer";
import {compose} from "redux";
import Preloader from "./components/common/Preloader/Preloader";
import store from "./redux/redux-store";
import {WithSuspense} from "./hoc/WithSuspense";

const DialogsContainer = React.lazy(() => import("./components/Dialogs/DialogsContainer"));
const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer"));


class App extends Component {
	catchAllUnhandleErrors = (promiseRejectionEvent, promise) => {
		alert(promiseRejectionEvent);
	};
	
	componentDidMount() {
		this.props.initializeApp();
		window.addEventListener("unhandlerejection", this.catchAllUnhandleErrors);
	}
	
	componentWillUnmount() {
		window.removeEventListener("unhandlerejection", this.catchAllUnhandleErrors);
	}
	
	render() {
		if (!this.props.initialized) {
			return <Preloader/>
		}
		
		return (
		  <div className='app-wrapper'>
			  <HeaderContainer/>
			  <Navbar/>
			  <div className='app-wrapper-content'>
				  <Switch>
					  <Route exact path='/'
								render={ () => <Redirect to={"/profile"} />}/>
					  
					  <Route path='/profile/:userId?'
								render={WithSuspense(ProfileContainer)}/>
					
					  <Route path='/dialogs'
								render={WithSuspense(DialogsContainer)}/>
					
					  <Route path='/users'
								render={() => <UsersContainer/>}/>
					
					  <Route path='/news'
								component={News}/>
								
					  <Route path='/music'
								component={Music}/>
								
					  <Route path='/settings'
								component={Settings}/>
								
					  <Route path='/login'
								render={() => <Login/>}/>
								
					  <Route path='*'
								render={() => <div>404 NOT FOUND</div>}/>
				  </Switch>
			  </div>
		  </div>
		);
	}
}

const mapStateToProps = (state) => ({
	initialized: state.app.initialized
});

let AppContainer = compose(
  withRouter,
  connect (mapStateToProps, {initializeApp})) (App);

const MainApp = (props) => {
	return (
	  <HashRouter>
		  <Provider store={store}>
			  <AppContainer />
		  </Provider>
	  </HashRouter>
	)
};

export default MainApp;