import {profileApi, usersApi} from "../api/api";
import {stopSubmit} from "redux-form";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
const SAVE_PROFILE_SUCCESS = 'SAVE_PROFILE_SUCCESS';

let initialState = {
	posts: [
		{id: 3, likesCounts: 15, message: 'Hi! How ara you?'},
		{id: 2, likesCounts: 20, message: 'It\'s my first post. It\'s my first post .It\'s my first post .It\'s my first post . It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post. It\'s my first post'},
		{id: 1, likesCounts: 1, message: 'Yooo, man!'},
	],
	profile: null,
	status: ''
};

const profileReducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD_POST: {
			let newPost = {
				id: state.posts[0].id + 1,
				likesCounts: 0,
				message: action.newPostText
			};
			return {
				...state,
				posts: [newPost, ...state.posts]
			}
		}
		case SET_STATUS: {
			return {
				...state, status: action.status
			};
		}
		case SET_USER_PROFILE: {
			return {
				...state, profile: action.profile
			};
		}
		case DELETE_POST: {
			return {
				...state, posts: state.posts.filter(p => p.id !== action.postId)
			};
		}
		case SAVE_PHOTO_SUCCESS: {
			return {
				...state, profile: {...state.profile, photos: action.photos}
			};
		}
		case SAVE_PROFILE_SUCCESS: {
			return {
				...state, profile: action.profile
			};
		}
		default:
			return state;
	}
};

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile}) ;
export const setStatus = (status) => ({type: SET_STATUS, status}) ;
export const deletePost = (postId) => ({type: DELETE_POST, postId}) ;
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos}) ;
export const saveProfileSuccess = (profile) => ({type: SAVE_PHOTO_SUCCESS, profile}) ;

export const getUserProfile = (userId) => async (dispatch) => {
	const data = await usersApi.getProfile(userId);
	dispatch(setUserProfile(data));
};

export const getStatus = (userId) => async (dispatch) => {
	const response = await profileApi.getStatus(userId);
	dispatch(setStatus(response.data));
};

export const updateStatus = (status) => async (dispatch) => {
	const response = await profileApi.updateStatus(status);
	
	if (response.data.resultCode === 0) {
		dispatch(setStatus(status));
	}
};

export const savePhoto = (file) => async (dispatch) => {
	const response = await profileApi.savePhoto(file);
	
	if (response.data.resultCode === 0) {
		dispatch(savePhotoSuccess(response.data.data.photos.large));
	}
};

export const saveProfile = (profile) => async (dispatch, getState) => {
	const userId = getState().auth.userId;
	const response = await profileApi.saveProfile(profile);
	
	if (response.data.resultCode === 0) {
		dispatch(getUserProfile(userId));
	} else {
		let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error";
		let contact = message
		.slice(
		  message.indexOf(">") + 1,
		  message.indexOf(")")
		)
		.toLocaleLowerCase();
		dispatch(stopSubmit("editProfile", {"contacts": {[contact]:  message}}));
		return Promise.reject( message);
	}
};

export default profileReducer;

