import * as axios from "axios";

const instance = axios.create({
	withCredentials: true,
	baseURL: 'https://social-network.samuraijs.com/api/1.0',
	headers: {"API-KEY": "8a6ad4af-f25c-4576-91b8-981c838cf476"},
});

export const usersApi = {
	getUsers(currentPage = 1, pageSize = 10) {
		return instance.get(`/users?page=${currentPage}&count=${pageSize}`)
		.then(response => response.data); // => {return response.data}
	},
	follow(userId) {
		return instance.post(`follow/${userId}`)
		.then(response => response.data)
	},
	unfollow(userId) {
		return instance.delete(`follow/${userId}`)
		.then(response => response.data)
	},
	getProfile(userId) {
		console.warn('Obsolete method. Please used profile obj.');
		return profileApi.getProfile(userId);
	}
};

export const profileApi = {
	getProfile(userId) {
		return instance.get(`profile/`+ userId)
		.then (response => response.data)
	},
	getStatus(userId) {
		return instance.get(`profile/status/`+ userId)
	},
	updateStatus(status) {
		return instance.put(`profile/status/`, {status: status})   // отправляет объект со свойством status
	},
	savePhoto(photoFile) {
		const formData = new FormData();
		formData.append("image", photoFile);
		return instance.put (`profile/photo`,formData, {
			headers: {
				'Content-Type': 'multipart/form-data'
			}
		})
	},
	saveProfile(photoFile) {
		return instance.put(`profile`, photoFile)
	}
};

export const authApi = {
	me(){
		return instance.get(`auth/me`);
	},
	login(email, password, rememberMe = false, captcha = null) {
		return instance.post(`auth/login`, {email, password, rememberMe, captcha})
	},
	logout() {
		return instance.delete(`auth/login`)
	},
};

export const securityApi = {
	getCaptchaUrl(){
		return instance.get(`security/get-captcha-url`);
	},
};

